import React from 'react'
import Typography from '@material-ui/core/Typography/Typography'
import CreateAnchorService from '../../services/create-anchor-service'

export default class DisplayH2 extends React.Component {
  render() {
    return (
      <Typography color="secondary" variant="h2" id={CreateAnchorService(this.props.content)}>
        {this.props.content}
      </Typography>
    )
  }
}
