import * as React from 'react'
import Link from 'react-router-dom/es/Link'
import getThemeService from '../services/get-theme-service'
import SiteService from '../services/site-service'

export default class ConditionalLink extends React.Component {
  render() {
    const { children, to, condition } = this.props
    let toRender

    const theme = getThemeService(SiteService.getSite())

    const colorLink = {
      color: theme.palette.primary.main,
    }

    if (condition) {
      toRender = (
        <Link className="hide-link" style={colorLink} to={`${to}`}>
          {children}
        </Link>
      )
    } else {
      toRender = <div>{children}</div>
    }

    return toRender
  }
}
