import * as React from 'react'
import Link from 'react-router-dom/es/Link'
import getThemeService from '../services/get-theme-service'
import SiteService from '../services/site-service'
import Typography from '@material-ui/core/Typography/Typography'

export default class DisplayFolderChildren extends React.Component {
  constructor(props) {
    super(props)
    this.state = { isFolder: !!this.props.article.children[0] }
  }

  render() {
    if (!this.state.isFolder) {
      return null
    }

    const childrenListSorted = this.props.article.children.sort((a, b) => {
      return a.order - b.order
    })

    const theme = getThemeService(SiteService.getSite())

    const colorLink = {
      color: theme.palette.primary.main,
    }

    return (
      <div className="folder-children-list">
        <hr />
        Ce dossier comprend les articles suivants :
        {childrenListSorted.map((child, index) => {
          return (
            <div key={index} className="folder-name">
              <Link style={colorLink} to={`/articles/${child.urlId}`}>
                <Typography variant="subtitle1">
                  {child.order + 1} - {child.title}
                </Typography>
              </Link>
            </div>
          )
        })}
        <hr className="folder-children-list-hr" />
      </div>
    )
  }
}
