export default function getContentPerTypeService(typeList, content) {
  /* Pour afficher l'article correctement, dans l'ordre et de façon sécure,
   on veut un tableau sous la forme {type, contenu} pour afficher l'article dans le bon ordre par type d'élément */
  const contentPerType = []
  let startPosition = 0
  // Pour cela, on commence par boucler tant qu'on a pas parcouru l'entièreté du coutenu
  while (startPosition < content.length) {
    // Puis on appelle cette fonction qui donne le premier élément depuis l'index startPos ainsi que
    // la position de fin de cet élément (à partir de laquelle on va repartir pour chercher d'autres éléments
    const res = getFirstElementFromStartPos(startPosition, typeList, content)

    const { type, element, startPos } = res
    startPosition = startPos

    contentPerType.push({ type: type, value: element })
  }
  return contentPerType
}

function getFirstElementFromStartPos(startPos, typeList, content) {
  /* Cette fonction renvoie un tableau contenant la position de tous les éléments définis dans le constructeur depuis startPos */
  const elementPos = getAllElementsPos(startPos, typeList, content)

  // On récupère la position de l'élément le plus proche
  let nearestTagPos = Math.min(...elementPos.filter(pos => pos !== -1))

  if (nearestTagPos === Infinity) {
    // Math.min renvoie infinity si aucune balise correspondante n'a été trouvée
    nearestTagPos = content.length
  }

  // On récupère son type
  let libelleType = typeList[elementPos.indexOf(nearestTagPos)]

  let element = ''
  let nextStartPos
  if (nearestTagPos === 0 || nearestTagPos === startPos) {
    // On enlève la balise de départ si on vient de détecter une balise

    nearestTagPos += ('[' + libelleType + ']').length
    const endPos = nearestTagPos + content.substring(nearestTagPos).indexOf('[/' + libelleType + ']')

    element = content.substring(nearestTagPos, endPos)
    nextStartPos = endPos + ('[/' + libelleType + ']').length
  } else {
    // Si la position trouvée est différente de zéro : on est dans du contenu interne à une balise racine
    // On récupère le contenu en faisant un substring
    element = content.substring(startPos, nearestTagPos)
    nextStartPos = nearestTagPos
    libelleType = undefined
  }

  // Qu'on renvoie au render, tout en donnant la position de fin (après la balise de fermeture
  return { type: libelleType, element, startPos: nextStartPos }
}

function getAllElementsPos(startPos, typeList, content) {
  const elementPos = []
  // Pour chaque type déclaré dans le constructeur, on récupère la position du premier élément qu'on trouve à partir de "startPos"
  typeList.forEach(type => {
    elementPos.push(content.indexOf('[' + type + ']', startPos))
  })

  return elementPos
}
