import React from 'react'
import baseBackendUrl from '../common/base-backend-url'
import Typography from '@material-ui/core/Typography'
import PropTypes from 'prop-types'
import { Fade, withStyles } from '@material-ui/core'
import CircularProgress from '@material-ui/core/CircularProgress/CircularProgress'
import Icon from '@material-ui/core/Icon'
import Flex from '../common/flex'
import DisplayArticleTile from '../common/display-article-tile'
import SnackbarsExtended from '../common/snackbar-extended'

const axios = require('axios')

const styles = {
  typography: {
    marginBottom: 20,
  },
  arrowScrollUp: {
    position: 'absolute',
    top: 60,
    zIndex: 1,
    fontSize: '2.5rem',
    borderRadius: '50%',
    backgroundColor: 'white',
    width: 30,
    height: 30,
    cursor: 'pointer',
  },
  arrowScrollDown: {
    position: 'absolute',
    bottom: 10,
    fontSize: '2.5rem',
    borderRadius: '50%',
    backgroundColor: 'white',
    width: 30,
    height: 30,
    zIndex: 1,
    cursor: 'pointer',
  },
}

class DisplaySimilarsList extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      similarArticles: null,
      wrapperScrollStyle: { top: 'auto' },
      showScrollArrowTop: false,
      showScrollArrowBottom: false,
      listScrollStyle: {},
      windowAbove1400: window.innerWidth > 1400,
      error: false,
    }
  }

  componentDidMount() {
    const { windowAbove1400 } = this.state

    this.getSimilars()

    if (windowAbove1400) {
      document.addEventListener('scroll', this.handleDocumentScroll)
    }
    window.addEventListener('resize', this.handleWindowResize)
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    const { windowAbove1400 } = this.state

    const similarList = document.getElementById('similars-list')
    if (similarList && windowAbove1400) {
      similarList.addEventListener('scroll', this.handleListScroll)
      this.handleListScroll()
    }
  }

  componentWillUnmount() {
    const { windowAbove1400 } = this.state

    if (windowAbove1400) {
      document.removeEventListener('scroll', this.handleDocumentScroll)
      const similarList = document.getElementById('similars-list')
      if (similarList) {
        similarList.removeEventListener('scroll', this.handleListScroll)
      }
    }
    window.removeEventListener('resize', this.handleWindowResize)
  }

  handleWindowResize = () => {
    const { windowAbove1400 } = this.state

    if (window.innerWidth > 1400 && !windowAbove1400) {
      document.addEventListener('scroll', this.handleDocumentScroll)
      this.setState({ windowAbove1400: true })
    } else if (window.innerWidth <= 1400 && windowAbove1400) {
      document.removeEventListener('scroll', this.handleDocumentScroll)
      const similarList = document.getElementById('similars-list')
      if (similarList) {
        similarList.removeEventListener('scroll', this.handleListScroll)
      }

      this.setState({
        windowAbove1400: false,
        wrapperScrollStyle: { top: 'auto' },
        listScrollStyle: {},
        showScrollArrowBottom: false,
        showScrollArrowTop: false,
      })
    }
  }

  handleDocumentScroll = () => {
    const { wrapperScrollStyle } = this.state
    if (window.scrollY >= 84 && wrapperScrollStyle.top === 'auto') {
      this.setState({
        wrapperScrollStyle: { top: 0, position: 'fixed' },
        listScrollStyle: { maxHeight: 'calc(100vh - 52px - 2rem - 54px)' },
      })
    } else if (window.scrollY < 84 && wrapperScrollStyle.top === 0) {
      this.setState({ wrapperScrollStyle: { top: 'auto', position: 'absolute' }, listScrollStyle: {} })
    }
  }

  handleListScroll = () => {
    const { showScrollArrowBottom, showScrollArrowTop } = this.state

    const similarList = document.getElementById('similars-list')
    const newShowScrollArrowBottom = similarList.scrollHeight - 1 > similarList.clientHeight + similarList.scrollTop
    const newShowScrollArrowTop = similarList.scrollTop > 0

    if (newShowScrollArrowTop !== showScrollArrowTop || newShowScrollArrowBottom !== showScrollArrowBottom) {
      this.setState({ showScrollArrowTop: newShowScrollArrowTop, showScrollArrowBottom: newShowScrollArrowBottom })
    }
  }

  getSimilars = async () => {
    try {
      const response = await axios.get(
        baseBackendUrl.getBaseBackendUrl() +
          '/articles/similars?articleId=' +
          this.props.article.id +
          '&' +
          baseBackendUrl.getSiteParam()
      )

      this.setState({ similarArticles: response.data })
    } catch (error) {
      this.setState({ error: true })
    }
  }

  handleArrowClick = direction => () => {
    const similarList = document.getElementById('similars-list')
    similarList.scrollTop = direction === 1 ? 0 : similarList.scrollHeight
  }

  handleCloseSnakbar = () => {
    this.setState({ error: false })
  }

  render() {
    const {
      similarArticles,
      wrapperScrollStyle,
      listScrollStyle,
      showScrollArrowTop,
      showScrollArrowBottom,
      windowAbove1400,
      error,
    } = this.state
    const { classes } = this.props

    if (!similarArticles) {
      return (
        <div className="similars-list-wrapper">
          <CircularProgress color="primary" />
          <SnackbarsExtended
            open={error}
            variant="error"
            onClose={this.handleCloseSnakbar}
            message="Une erreur est survenue, veuillez recharger la page"
          />
        </div>
      )
    } else if (!similarArticles[0]) {
      return null
    } else {
      return (
        <div className="similars-list-wrapper" style={wrapperScrollStyle}>
          <Typography className={classes.typography} variant="h2">
            {' '}
            Articles similaires :
          </Typography>
          <div className="similars-list" id="similars-list" style={listScrollStyle}>
            <Fade in={showScrollArrowTop} timeout={300}>
              <Flex
                className={classes.arrowScrollUp}
                alignItems="center"
                justify="center"
                onClick={this.handleArrowClick(1)}
              >
                <Icon>expand_less</Icon>
              </Flex>
            </Fade>
            {similarArticles.map((similarArticle, i) => (
              <DisplayArticleTile
                key={i}
                article={similarArticle}
                isSimilarList={true}
                windowAbove1400={windowAbove1400}
              />
            ))}
            <Fade in={showScrollArrowBottom} timeout={300}>
              <Flex
                className={classes.arrowScrollDown}
                alignItems="center"
                justify="center"
                onClick={this.handleArrowClick(-1)}
              >
                <Icon>expand_more</Icon>
              </Flex>
            </Fade>
          </div>
        </div>
      )
    }
  }
}

DisplaySimilarsList.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(DisplaySimilarsList)
