import { Link } from 'react-router-dom'
import React from 'react'
import withStyles from '@material-ui/core/styles/withStyles'
import Icon from '@material-ui/core/Icon'
import DisplayParagraph from './display-paragraph'
import { formatThousands } from './formatThousands'
import Flex from './flex'
import Typography from '@material-ui/core/Typography'
import Fab from '@material-ui/core/Fab'

const styles = {
  articleImage: {
    objectFit: 'cover',
    maxWidth: 350,
    maxHeight: 300,
    width: '90vw',
  },
  articleContent: {
    textAlign: 'left',
    paddingLeft: 20,
    width: 'calc(68vw - 350px - 20px)',
    '@media (max-width: 1400px)': {
      width: 'calc(90vw - 350px - 20px)',
    },
    '@media (max-width: 1015px)': {
      width: 'calc(90vw)',
      paddingLeft: 0,
      paddingTop: 10,
    },
  },
  articleTile: {
    padding: '25px 0 25px 0',
    borderTop: '1px solid #ccc',
  },
  iconFolder: {
    paddingTop: '4px !important',
  },
  articleIcons: {
    textAlign: 'right',
    marginTop: -5,
  },
  articleIcon: {
    marginLeft: 5,
    '&> span': {
      paddingTop: 2,
    },
  },
  articleTags: {
    marginTop: 15,
  },
  articleDescription: {
    marginTop: 15,
  },
  alignRight: {
    textAlign: 'right',
  },
  articleInfos: {
    marginBottom: 10,
    fontSize: '1rem',
  },
}

class DisplayArticleTile extends React.Component {
  render() {
    const { article, classes, isSimilarList, windowAbove1400 } = this.props

    const overrideCssImage = {}
    let overrideCssContent = {}

    if (isSimilarList && windowAbove1400 !== undefined && windowAbove1400) {
      overrideCssImage.width = '90%'
      overrideCssImage.paddingLeft = 0
      overrideCssContent = overrideCssImage
      overrideCssContent.paddingTop = 10
    }

    return (
      <div className={classes.articleTile}>
        <Flex direction="row" wrap justify="center" alignItems="center">
          <img
            src={article.descriptionImage}
            style={overrideCssImage}
            className={classes.articleImage}
            alt={article.title}
          />

          <div className={classes.articleContent} style={overrideCssContent}>
            <Link to={`/articles/${article.urlId}`} className="hide-link">
              <Typography variant="caption" className={classes.articleInfos}>
                <Flex justify="space-between">
                  {article.creationDate}
                  <span className={classes.articleIcons}>
                    <span className={classes.articleIcon}>
                      {article.children.length > 0 && (
                        <Icon className={classes.iconFolder} style={{ fontSize: 20 }}>
                          folder
                        </Icon>
                      )}
                      {article.parent && (
                        <Icon className={classes.iconFolder} style={{ fontSize: 20 }}>
                          folder_open
                        </Icon>
                      )}
                    </span>
                    <span className={classes.articleIcon}>
                      <Icon className="icon-with-value" style={{ fontSize: 16 }}>
                        thumb_up
                      </Icon>
                      <span>{formatThousands(article.upvotes)}</span>
                    </span>
                    <span className={classes.articleIcon}>
                      <Icon className="icon-with-value" style={{ fontSize: 16 }}>
                        visibility
                      </Icon>
                      <span>{formatThousands(article.views)}</span>
                    </span>
                  </span>
                </Flex>
              </Typography>
              <Typography variant="h2" className="link-with-underline" color="primary">
                {article.title}
              </Typography>
              <DisplayParagraph
                content={article.description}
                className={classes.articleDescription}
                isDescription={true}
              />
            </Link>
            <Flex justify="space-evenly" className={classes.articleTags}>
              {article.tags
                .filter(tag => tag.weight === 1)
                .map((tag, index) => (
                  <Fab key={index} component={Link} to={`/${tag.label}`} variant="extended" color="secondary">
                    {tag.label}
                  </Fab>
                ))}
            </Flex>
          </div>
        </Flex>
      </div>
    )
  }
}

export default withStyles(styles)(DisplayArticleTile)
