import { Link } from 'react-router-dom'
import React from 'react'
import Fab from '@material-ui/core/Fab'

export default class DisplayArticleTagList extends React.Component {
  render() {
    const article = this.props.article

    return (
      <div className="article-footer-tag-list">
        <h3>Mots clés :</h3>

        {article.tags.map((tag, i) => (
          <Fab component={Link} key={i} to={`/${tag.label}`} variant="extended" color="secondary" className="badge">
            {tag.label}
          </Fab>
        ))}
      </div>
    )
  }
}
