function initSiteName() {
  const redirect = window.location.href.indexOf('sup-vision')
  if (redirect !== -1) {
    return window.location.replace(
      `${window.location.href.substring(0, redirect)}support-vision.fr/${window.location.href.substring(
        redirect + 10 + 10
      )}`
    ) // 10 pour sup-vision et 10 autres pour .infos.st/
  }

  if (window.location.href.indexOf('supra.support-vision') !== -1) {
    return 'supra'
  }
  if (window.location.href.indexOf('super.support-vision') !== -1) {
    return 'super'
  }
  if (window.location.href.indexOf('supple.support-vision') !== -1) {
    return 'supple'
  }

  return null
}

export default class SiteService {
  static site = initSiteName()

  static getSite() {
    return this.site
  }
}
