import React from 'react'
import ShowArticleList from './show-article-list.js'
import SearchArticles from './search-articles.js'
import InfiniteScroll from 'react-infinite-scroller'
import baseBackendUrl from '../common/base-backend-url'
import CircularProgress from '@material-ui/core/CircularProgress/CircularProgress'
import { format } from '../common/format'
import { Icon } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import withStyles from '@material-ui/core/styles/withStyles'
import SnackbarsExtended from '../common/snackbar-extended'
import siteService from '../services/site-service'

const axios = require('axios')

const styles = {
  button: {
    marginTop: '2rem',
    marginBottom: '2rem',
  },
}

class FilterableListeArticles extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      articleList: null,
      selectedTag: [],
      hasMore: false,
      url: null,
      page: 0,
      askToContinue: false,
      error: false,
    }
    if (this.props.match && this.props.match.params && this.props.match.params.tag) {
      this.state.selectedTag = [props.match.params.tag]
    }
    document.title = 'Support Vision'
    const canonical = document.querySelector('link[rel="canonical"]')
    if (canonical !== null) {
      canonical.href = 'https://' + siteService.getSite() + '.support-vision.fr'
    }
  }

  componentWillReceiveProps(newProps, nextContext) {
    if (newProps.match && newProps.match.params && newProps.match.params.tag) {
      this.setState({
        articleList: null,
        selectedTag: [newProps.match.params.tag],
        hasMore: false,
        url: null,
        page: 0,
        error: false,
      })
    } else {
      this.setState({ articleList: null, selectedTag: [], hasMore: false, url: null, page: 0, error: false })
    }
    document.title = 'Support-Vision'
    const canonical = document.querySelector('link[rel="canonical"]')
    if (canonical !== null) {
      canonical.href = 'https://' + siteService.getSite() + '.support-vision.fr'
    }
  }

  OnSearchChange = url => {
    this.setState({ url: url, page: 0, articleList: null, hasMore: true })
  }

  handleContinue = () => {
    this.getNewPage()
  }

  getNewPage = async () => {
    const { page, askToContinue } = this.state

    if (page % 5 === 0 && page !== 0 && !askToContinue) {
      // Affichage d'un bouton qui demande si on veut continuer ou non le scroll infini
      return this.setState({ hasMore: false, askToContinue: true })
    } else if (askToContinue) {
      this.setState({ askToContinue: false })
    }

    let itemsPerPage = 2

    if (window.outerWidth >= 840 && window.outerWidth <= 1250) {
      itemsPerPage = 4
    } else if (window.outerWidth > 1250) {
      itemsPerPage = 6
    }

    try {
      const response = await axios.get(
        baseBackendUrl.getBaseBackendUrl() +
          '/articles' +
          this.state.url +
          '&page=' +
          page +
          '&itemsPerPage=' +
          itemsPerPage +
          '&' +
          baseBackendUrl.getSiteParam()
      )

      response.data.articles.forEach(article => {
        article.creationDate = format(article.creationDate, 'D MMMM YYYY')
        if (article.modificationDate) {
          article.modificationDate = format(article.modificationDate, 'D MMMM YYYY')
        }
      })

      this.setState(prevState => ({
        articleList: prevState.articleList
          ? prevState.articleList.concat(response.data.articles)
          : response.data.articles,
        hasMore: response.data.hasMore,
        page: prevState.page + 1,
      }))
    } catch (error) {
      this.setState({ error: true, hasMore: false })
    }
  }

  handleCloseSnakbar = () => {
    this.setState({ error: false })
  }

  render() {
    const { classes } = this.props
    const { hasMore, articleList, selectedTag, askToContinue, error } = this.state

    return (
      <div className="filterable-liste-articles">
        <SearchArticles onFormChange={this.OnSearchChange} selectedTag={selectedTag} />
        <InfiniteScroll
          pageStart={0}
          loadMore={this.getNewPage.bind(this)}
          hasMore={hasMore}
          loader={
            <div className="loader" key={0}>
              <CircularProgress color="primary" />
            </div>
          }
        >
          <ShowArticleList articleList={articleList} />
        </InfiniteScroll>
        {askToContinue && (
          <Button color="secondary" variant="text" className={classes.button} onClick={this.handleContinue}>
            Afficher d'autres articles <Icon>arrow_drop_down</Icon>
          </Button>
        )}
        <SnackbarsExtended
          open={error}
          variant="error"
          onClose={this.handleCloseSnakbar}
          message="Une erreur est survenue, veuillez recharger la page"
        />
      </div>
    )
  }
}

export default withStyles(styles)(FilterableListeArticles)
