const CreateAnchorService = header => {
  header += ' '
  const anchor = header
    .substring(0, 50)
    .toLowerCase()
    .replace(/\u00a0/g, '-')
    .replace(/[ '’`]/g, '-')
    .replace(/[#?\\/()!"«»~+$€<>=;*£.&{}|,°:]/g, '')
    .replace(
      /(\[a\])|(\[\/a\])|(\[href\][A-Za-z0-9\-_.:/]+\[\/href\])|(\[text\])|(\[\/text\])|(\[strong\])|(\[\/strong\])|(\[i\])|(\[\/i\])|(\[s\])|(\[\/s\])/g,
      ''
    )
    .replace(/--+/g, '-')
    .replace(/[àâ]/g, 'a')
    .replace(/[éèê]/g, 'e')
    .replace(/[î]/g, 'i')
    .replace(/[ô]/g, 'o')
    .replace(/[ç]/g, 'c')
    .replace(/[ùû]/g, 'u')

  return anchor.substring(0, anchor.lastIndexOf('-')) // Pas de mots coupés à la fin
}

export default CreateAnchorService
