import React from 'react'
import DisplayArticleComponents from './content/display-article-components'
import DisplayFolderBrothers from './display-folder-brothers'

export default class DisplayArticleContent extends React.Component {
  render() {
    const article = this.props.article

    return (
      <div className="content-article">
        <img alt="img-principale" src={article.descriptionImage} className="img-principale" />
        <DisplayFolderBrothers article={article} />
        <DisplayArticleComponents content={article.content} />
      </div>
    )
  }
}
