import Icon from '@material-ui/core/Icon/Icon'
import React from 'react'
import CookieService from '../services/cookie-service'
import baseBackendUrl from '../common/base-backend-url'
import Fab from '@material-ui/core/Fab'
import SnackbarsExtended from '../common/snackbar-extended'

const axios = require('axios')

export default class DisplayArticleVotes extends React.Component {
  constructor(props) {
    super(props)

    this.state = { alreadyUpvoted: false, alreadyDownvoted: false, error: false }
  }

  componentDidMount() {
    const cookieVal = atob(CookieService.getCookie('dldkljsfpngi'))
    const alreadyVoted = cookieVal.indexOf(this.props.article.id)

    if (alreadyVoted !== -1) {
      cookieVal.substring(alreadyVoted + 1, 5) === 'true'
        ? this.setState({ alreadyUpvoted: true })
        : this.setState({ alreadyDownvoted: true })
    }
  }

  handleUpvote = async () => {
    let sens = this.state.alreadyUpvoted ? -1 : 1

    try {
      axios.put(
        baseBackendUrl.getBaseBackendUrl() +
          '/articles/' +
          this.props.article.urlId +
          '/votes' +
          '?' +
          baseBackendUrl.getSiteParam() +
          '&dldkljsfpngi=' +
          sens +
          '&dldkljsfpngi2=1'
      )
      this.setState(prevState => ({
        alreadyUpvoted: !prevState.alreadyUpvoted,
      }))

      let cookieVal = atob(CookieService.getCookie('dldkljsfpngi'))
      const alreadyVoted = cookieVal.indexOf(this.props.article.id)

      if (alreadyVoted === -1) {
        cookieVal += this.props.article.id + 'true,'
      } else {
        cookieVal = cookieVal.replace(this.props.article.id + 'true,', '')
      }

      CookieService.setCookie({ key: 'dldkljsfpngi', value: cookieVal })
    } catch (error) {
      this.setState({ error: true })
    }
  }

  handleDownvote = async () => {
    let sens = this.state.alreadyDownvoted ? -1 : 1

    try {
      await axios.put(
        baseBackendUrl.getBaseBackendUrl() +
          '/articles/' +
          this.props.article.urlId +
          '/votes' +
          '?' +
          baseBackendUrl.getSiteParam() +
          '&dldkljsfpngi=' +
          sens +
          '&dldkljsfpngi2=-1'
      )
      this.setState(prevState => ({
        alreadyDownvoted: !prevState.alreadyDownvoted,
      }))

      let cookieVal = atob(CookieService.getCookie('dldkljsfpngi'))
      const alreadyVoted = cookieVal.indexOf(this.props.article.id)

      if (alreadyVoted === -1) {
        cookieVal += this.props.article.id + 'false,'
      } else {
        cookieVal = cookieVal.replace(this.props.article.id + 'false,', '')
      }

      CookieService.setCookie({ key: 'dldkljsfpngi', value: cookieVal })
    } catch (error) {
      this.setState({ error: true })
    }
  }

  handleCloseSnakbar = () => {
    this.setState({ error: false })
  }

  render() {
    const { alreadyUpvoted, alreadyDownvoted, error } = this.state
    const { article } = this.props

    let colorUpvote = 'secondary'
    let colorDownvote = 'secondary'

    if (alreadyUpvoted) {
      colorUpvote = 'primary'
    }
    if (alreadyDownvoted) {
      colorDownvote = 'primary'
    }

    return (
      <div className="article-footer-votes">
        <h3>{article.children[0] ? <span>Ce dossier vous plaît </span> : <span>Cet article vous a plu </span>}?</h3>
        <Fab color={colorUpvote} disabled={alreadyDownvoted} onClick={() => this.handleUpvote()}>
          <Icon>thumb_up</Icon>
        </Fab>
        <Fab color={colorDownvote} disabled={alreadyUpvoted} onClick={() => this.handleDownvote()}>
          <Icon>thumb_down</Icon>
        </Fab>
        <SnackbarsExtended
          open={error}
          variant="error"
          onClose={this.handleCloseSnakbar}
          message="Une erreur est survenue, veuillez recharger la page"
        />
      </div>
    )
  }
}
