import * as React from 'react'
import Typography from '@material-ui/core/Typography/Typography'
import DisplayLink from './content/display-link'

export default class DisplayDonationButton extends React.Component {
  render() {
    const linkToBatArticle =
      "[href]https://supple.support-vision.fr/articles/comment-le-navigateur-brave-compte-r%C3%A9volutionner-i-19[/href][text]Qu'est-ce que c'est ?[/text]"

    return (
      <div>
        <Typography variant="body2">
          Ce site ne comporte <b>aucune pub et est maintenu bénévolement</b>. Si vous souhaitez me soutenir, n'hésitez
          pas à m'offrir une <b>baguette (soit 1€) !</b>
        </Typography>
        <form action="https://www.paypal.com/cgi-bin/webscr" method="post" target="_top">
          <input type="hidden" name="cmd" value="_s-xclick" />
          <input type="hidden" name="hosted_button_id" value="7H4GSHNZNEGLU" />
          <input
            type="image"
            src="https://www.paypalobjects.com/fr_FR/FR/i/btn/btn_donate_LG.gif"
            border="0"
            name="submit"
            alt="PayPal, le réflexe sécurité pour payer en ligne"
          />
          <img alt="" border="0" src="https://www.paypalobjects.com/fr_FR/i/scr/pixel.gif" width="1" height="1" />
        </form>
        <Typography variant="body2">
          Sinon si vous êtes sur <b>Brave</b>, n'hésitez pas à me faire un <b>don d'1 BAT !</b> (
          <DisplayLink content={linkToBatArticle} />)
        </Typography>
      </div>
    )
  }
}
