import * as React from 'react'
import CookieService from '../services/cookie-service'
import Button from '@material-ui/core/Button/Button'
import Typography from '@material-ui/core/Typography/Typography'

export default class DisplayGDPRBanner extends React.Component {
  constructor(props) {
    super(props)
    this.state = { display: false }
  }

  componentDidMount() {
    if (CookieService.getCookie('gdprBanner') === '') {
      this.setState({ display: true })
    }
  }

  handleOk() {
    CookieService.setCookie({ key: 'gdprBanner', value: 'coucou toi qui regarde les cookies' }, true)
    this.setState({ display: false })
  }

  render() {
    if (!this.state.display) {
      return null
    }

    return (
      <div className="gdprBanner">
        <Typography variant="body2">
          Ce site n'enregistre aucune de vos données personnelles (eh oui, pour une fois !) et ne comporte aucune pub.
          Il est donc conforme à la loi RGPD (Européenne). Il utilise néammoins quelques cookies pour son bon
          fonctionnement technique.
        </Typography>
        <Button variant="raised" onClick={() => this.handleOk()}>
          Ok
        </Button>
      </div>
    )
  }
}
