import React from 'react'
import './details-article.css'
import baseBackendUrl from '../common/base-backend-url'
import CookieService from '../services/cookie-service'
import DisplayArticleHeader from './display-article-header'
import DisplayArticleMain from './display-article-main'
import DisplaySimilarsList from './display-similars-list'
import CircularProgress from '@material-ui/core/CircularProgress/CircularProgress'
import { Typography } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import Icon from '@material-ui/core/Icon'
import getThemeService from '../services/get-theme-service'
import SiteService from '../services/site-service'
import { format } from '../common/format'
import Flex from '../common/flex'
import withStyles from '@material-ui/core/styles/withStyles'
import { Link } from 'react-router-dom'
import SnackbarsExtended from '../common/snackbar-extended'

const axios = require('axios')

const styles = {
  error404: {
    height: 'calc(100vh - 64px - 64px)',
  },
}

class DetailsArticle extends React.Component {
  constructor(props) {
    super(props)
    this.state = { loading: true, article: null, error: false }

    this.getArticle()
  }

  componentWillReceiveProps(newProps, nextContext) {
    this.setState({ loading: true, article: null }, () => {
      this.getArticle()
    })
  }

  getArticle = async () => {
    const cookie = atob(CookieService.getCookie('kljfpngidlds'))
    const viewed = cookie.includes(this.props.match.params.urlId)

    try {
      const response = await axios.get(
        baseBackendUrl.getBaseBackendUrl() +
          '/articles/' +
          this.props.match.params.urlId +
          '?' +
          baseBackendUrl.getSiteParam() +
          '&kljfpngidlds=' +
          viewed
      )
      if (response.data) {
        response.data.creationDate = format(response.data.creationDate, 'D MMMM YYYY')
        if (response.data.modificationDate) {
          response.data.modificationDate = format(response.data.modificationDate, 'D MMMM YYYY')
        }
        document.title = response.data.title + ' - Support-Vision'
        const canonical = document.querySelector('link[rel="canonical"]')
        if (canonical !== null) {
          canonical.href = 'https://' + SiteService.getSite() + '.support-vision.fr/articles/' + response.data.urlId
        }
      }

      this.setState({ article: response.data, loading: false })

      if (viewed === false) {
        CookieService.setCookie({ key: 'kljfpngidlds', value: cookie + response.data.urlId + ',' })
      }
    } catch (error) {
      this.setState({ error: true })
    }
  }

  handleCloseSnakbar = () => {
    this.setState({ error: false })
  }

  render() {
    const { article, error, loading } = this.state
    const { classes } = this.props
    const site = SiteService.getSite()
    const theme = getThemeService(site)

    const inlineBackgroundColor = {
      backgroundColor: theme.palette.tertiary.main,
    }

    if (loading) {
      return (
        <div className="article-page-wrapper">
          <div className="article-wrapper">
            <CircularProgress color="primary" />
          </div>
          <SnackbarsExtended
            open={error}
            variant="error"
            onClose={this.handleCloseSnakbar}
            message="Une erreur est survenue, veuillez recharger la page"
          />
        </div>
      )
    } else if (!article) {
      return (
        <Flex className={classes.error404} justify="center" alignItems="center" direction="column">
          <Typography variant="h2">Erreur, l'article correspondant n'a pas été trouvé.</Typography>
          <Button component={Link} to="/" color="secondary">
            <Icon>arrow_back_ios</Icon>Retour
          </Button>
        </Flex>
      )
    } else {
      return (
        <div className="article-page-wrapper">
          <div className="article-wrapper">
            <div className="text-align-left">
              <Button component={Link} to="/" color="secondary">
                <Icon>arrow_back_ios</Icon>Retour
              </Button>
            </div>
            <div style={inlineBackgroundColor}>
              <DisplayArticleHeader article={article} />
              <DisplayArticleMain article={article} />
            </div>
          </div>
          <DisplaySimilarsList article={article} />
        </div>
      )
    }
  }
}

export default withStyles(styles)(DetailsArticle)
